import React, { FunctionComponent as FC } from 'react';
import { useSelector } from 'react-redux'; // To call Action Creators
import { State } from '../../../state';
import _ from 'lodash';
import './index.scss';

import { textDict } from '../../../types/data.global';

interface HomepageProps {
  height: number;
}

const Homepage: FC<HomepageProps> = (props) => {
  const { height } = props

  const selectedLanguage: string = useSelector((state: State) => state.uiReducer?.language) as string
  const languageVersion = textDict[selectedLanguage as ('English' | 'Chinese')]

  const introElementList: JSX.Element[] = languageVersion.textIntro.intro.map((paragraph: string) => (
    <div className='homepage-paragraph' style={{fontFamily: 'alibaba-puhuiti-light'}}>{paragraph}</div>
  ))

  const workElementList: JSX.Element[] = _.range(3).map((index: number) => {
    return (
      <div className='work-unit'>
        <div className='image-div'>
          <img className='image' src={languageVersion.workList[index].imageUrl} alt={languageVersion.workList[index].title} />
        </div>
        <div className='title-group'>
          <div className='title' style={{color: languageVersion.workList[index].titleColor}}>{languageVersion.workList[index].title}</div>
          <div className='subtitle'>{languageVersion.workList[index].subtitle}</div>
        </div>
      </div>
    )
  })

  return (
    <div className='homepage-div' style={{height: height}}>
      <div className='homepage-intro'>
        <div className='intro-title'>{languageVersion.textIntro.title}</div>
        {introElementList}
      </div>
      <div className='homepage-teaser'>{workElementList}</div>
    </div>
  )
}


export default Homepage;