import React, { FunctionComponent as FC } from 'react';
import _ from 'lodash';
import './index.scss';


interface WorkpageProps {
  height: number,
  title: string,
  subtitle: string,
  intro: string[],
  titleColor: string,
  imgUrl: string
}

const Workpage: FC<WorkpageProps> = (props) => {
  const { height, title, subtitle, intro, titleColor, imgUrl } = props

  const introGroup: JSX.Element[] = intro.map((paragraph: string) => (
    <div className='paragraph' style={{fontFamily: 'alibaba-puhuiti-light'}}>{paragraph}</div>
  ))

  return (
    <div className='work-page' style={{height: height}}>
      <div className='text-intro'>
        <div className='title' style={{color: titleColor}}>{title}</div>
        <div className='subtitle'>{subtitle}</div>
        {introGroup}
      </div>
      <div className='image-group'>
        <img className='image' src={imgUrl} alt={title} />
      </div>
    </div>
  )
}


export default Workpage;