import React, { FunctionComponent as FC, useState, useEffect } from 'react';
import { useWindowSize } from 'usehooks-ts';
import { useDispatch, useSelector } from 'react-redux'; // To call Action Creators
import { State } from '../state';
import { bindActionCreators } from 'redux';
import { actionCreators } from '../state';
import _ from 'lodash';
import 'antd/dist/antd.min.css';
import './App.scss';

import Homepage from './components/Homepage';
import Workpage from './components/Workpage';
import Authorpage from './components/Authorpage';
import { textDict } from '../types/data.global';

import { Layout, Radio, RadioChangeEvent } from 'antd';

const { Header, Footer, Content } = Layout;

function App() {
  const windowSize = useWindowSize()
  const headerFooterHeight: number = 64
  const [navbarColor, setNavbarColor] = useState<string>('#272e40')

  // store
  const dispatch = useDispatch()
  const { onChangeLanguage } = bindActionCreators(actionCreators, dispatch)
  const selectedLanguage: string = useSelector((state: State) => state.uiReducer?.language) as string
  console.log('selectedLanguage: ', selectedLanguage)
  const languageVersion = textDict[selectedLanguage as ('English' | 'Chinese')]

  const changeBackground = () => {
    if (window.scrollY >= 54) {
      setNavbarColor('#3232327d')
    } else {
      setNavbarColor('#323232')
    }
  }
  const handleLanguageChange = (e: RadioChangeEvent) => {
    if (e.target.value === 'English') {
      onChangeLanguage('English')
    } else {
      onChangeLanguage('Chinese')
    }
  }

  const workpageList: JSX.Element[] = _.range(3).map((index: number) => (
    <Workpage height={windowSize.height - headerFooterHeight}
      title={languageVersion.workList[index].title}
      subtitle={languageVersion.workList[index].subtitle}
      intro={languageVersion.workList[index].intro}
      titleColor={languageVersion.workList[index].titleColor}
      imgUrl={languageVersion.workList[index].imageUrl}
    />
  ))

  return (
    <div className='App'>
      <Layout>
        <Header className='header' style={{
          position: 'fixed',
          zIndex: 1,
          width: '100%',
          backgroundColor: navbarColor,
          height: headerFooterHeight,
        }}>
          <div className='header-group'>
            <div className='logo'>Logo Placeholder</div>
            <div className='menu-group'>
              <div className='homepage menu'>Homepage</div>
              <div className='work1 menu'></div>
              <div className='work2 menu'></div>
              <div className='work3 menu'></div>
              <div>
                <Radio.Group value='small' onChange={(e) => handleLanguageChange(e)}>
                  <Radio.Button value="English">English</Radio.Button>
                  <Radio.Button value="Chinese"></Radio.Button>
                </Radio.Group>
              </div>
            </div>
          </div>
        </Header>
        <Content className='content' style={{
          width: '100%',
        }}>
          <Homepage height={windowSize.height} />
          {workpageList}
          <Authorpage height={windowSize.height} />
        </Content>
        <Footer className='footer' style={{
          width: '100%',
          backgroundColor: navbarColor,
          height: headerFooterHeight,
        }}>
          <span>Copyright  2022 Junxiu</span>
        </Footer>
      </Layout>
    </div>
  );
}

export default App;
